/* ====================
    VARIABLES
==================== */
:root {
    --gutter: 15px;

    --color-dark: #333333;
    --color-light: #4f4f4f;
    --color-ultra-light: #828282;
    --color-gray-100: #f2f2f2;
    --color-gray-200: #bdbdbd;
    --color-primary: rgba(235, 87, 87, 0.72);

    --font-family-content: 'Montserrat', sans-serif;
    --font-family-secondary: 'Mulish', sans-serif;
    --font-style-content: normal;
}

/* ====================
    CONTAINER
==================== */
.container {
    position: relative;
    width: 100%;
    padding-right: 15px;
    padding-right: var(--gutter);
    padding-left: 15px;
    padding-left: var(--gutter);
    margin-top: 1.1rem;
    margin-right: auto;
    margin-left: auto;
    min-height: calc(100vh - 1.1rem);
}

@media (min-width: 768px) {
    .container {
        max-width: 720px;
    }
}
@media (min-width: 992px) {
    .container {
        max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .container {
        max-width: 1140px;
    }
}

