*,
*:before,
*:after {
    box-sizing: border-box;
    outline: none;
    padding: 0;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font-family: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

button {
    cursor: pointer;
}

button:focus {
    outline: none;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: var(--font-family-content);
    font-style: normal;
    font-weight: normal;
    color: var(--color-dark);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
}

p,
input {
    font-size: 0.8rem;
    line-height: 1.1;
}

@media (min-width: 768px) {
    p {
        font-size: 0.9rem;
    }
}

input {
    font-family: var(--font-family-secondary);
    font-style: normal;
    font-weight: normal;
    color: var(--color-dark);
}

input,
button {
    background: transparent;
    border: none;
}
